html {
  height: 100%;
  width: 100%;
  --text-color: #ecf0f1;
  --action-color: #e74c3c;
}

* {
  box-sizing: border-box;
  outline: 0;
  margin: 0;
}

body {
  height: 100%;
  margin: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.page {
  width: 100%;
  height: 100%;
  background: darkblue;
  padding: 60px;
}

.scene-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.scene {
  border: 3px solid var(--text-color);
  border-color: white;
  border-radius: 10px;
}

.block {
  stroke-width: 2px;
  stroke: white;
}

.paddle {
  fill: lightslategray;
}

.ball {
  fill: white;
}

.level {
  font-weight: normal;
  font-size: 23px;
  fill: whitesmoke;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

.life {
  fill: red;
}
